import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ClienteComponent } from './cliente.component';
import { AngularCropperjsModule } from 'angular-cropperjs';
import { CarouselModule } from 'ngx-owl-carousel-o';
import { SweetAlert2Module } from '@sweetalert2/ngx-sweetalert2';
import { NgSelectModule } from '@ng-select/ng-select';
import { ComprasComponent } from './compras/compras.component';
import { SimplemdeModule, SIMPLEMDE_CONFIG } from 'ng2-simplemde'
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { EstadisticasComponent } from './estadisticas/estadisticas.component';
import { NgApexchartsModule } from "ng-apexcharts";
import { CalendarioComponent } from './calendario/calendario.component';

import { FullCalendarModule } from '@fullcalendar/angular';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid';
import listPlugin from '@fullcalendar/list';
import interactionPlugin from '@fullcalendar/interaction';
import { CotizacionComponent } from './cotizacion/cotizacion.component';
import { ReactiveFormsModule } from '@angular/forms';
import { ComplaintsComponent } from './compras/complaints/complaints.component';

FullCalendarModule.registerPlugins([
  dayGridPlugin,
  timeGridPlugin,
  listPlugin,
  interactionPlugin
])

const routes: Routes = [
  { path: '', redirectTo: '/cliente/catalogo', pathMatch: 'full'},
  {
    path: "", loadChildren: () => import("../catalogo/catalogo.module").then(m => m.CatalogoModule)
  },
  {
    path: "", loadChildren: () => import("../perfil/perfil.module").then(m => m.PerfilModule)
  },
  {
    path: "compras", component: ComprasComponent
  },
  {
    path: "estadisticas", component: EstadisticasComponent
  },
  {
    path: "calendario", component: CalendarioComponent
  },
  {
    path: "cotizaciones", component: CotizacionComponent
  },
  {
    path: "complaints/:ventaId/:pedidoId", component: ComplaintsComponent
  }
];
@NgModule({
  declarations: [
    ClienteComponent,
    ComprasComponent,
    EstadisticasComponent,
    CalendarioComponent,
    CotizacionComponent,
    ComplaintsComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    AngularCropperjsModule,
    CarouselModule,
    SweetAlert2Module.forRoot(),
    NgSelectModule,
    NgbModule,
    FullCalendarModule,
    SimplemdeModule.forRoot({
      provide: SIMPLEMDE_CONFIG,
      useValue: {}
    }),
    NgApexchartsModule,
    ReactiveFormsModule
  ]
})
export class ClienteModule { }
