import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { CalendarioService } from '../../../../services/calendario.service';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import { CalendarOptions, EventInput, EventClickArg } from '@fullcalendar/angular';
import esLocale from '@fullcalendar/core/locales/es';
import Swal from 'sweetalert2';
import * as moment from 'moment';

@Component({
  selector: 'app-calendario',
  templateUrl: './calendario.component.html',
  styleUrls: ['./calendario.component.scss']
})
export class CalendarioComponent implements OnInit {

  private mensajesComponent: MensajesComponent = new MensajesComponent(this.router);
  public eventGuid: number = 0;
  public calendarEvents: EventInput[] = [];
  public fechasEnvio: any = [];
  public fechasLlegada: any = [];
  public dataCalendar: any = [];

  @ViewChild('calendar') calendarComponent: CalendarioComponent;

  public calendarOptions: CalendarOptions = {
    headerToolbar: {
      left: 'prev,today,next',
      center: 'title',
      right: 'dayGridMonth,timeGridWeek,timeGridDay,listWeek'
    },
    customButtons: {
      next: {
        click: this.nextMonth.bind(this)
      },
      prev: {
        click: this.prevMonth.bind(this)
      },
      today: {
        text: "Hoy",
        click: this.currentMonth.bind(this),
      }
    },
    locale: esLocale,
    initialView: 'dayGridMonth',
    weekends: true,
    editable: false,
    selectable: true,
    selectMirror: true,
    dayMaxEvents: true,
    eventClick: this.handleEventClick.bind(this),
    /* you can update a remote database when these fire:
    select: this.handleDateSelect.bind(this),
    eventsSet: this.handleEvents.bind(this)
    eventAdd:
    eventChange:
    eventRemove:
    */
  };

  constructor(private calendarioService: CalendarioService, private router: Router) { }

  ngOnInit(): void {
    LoaderComponent.status = true;
    const date = moment().format("YYYY-MM-DD");
    this.checkCalendarItems(date);
  }

  public checkCalendarItems(date): void {
    this.calendarioService.getVentasNotFinished(date).subscribe(
      response => {
        this.dataCalendar = response;
        this.fechasEnvio = [];
        this.fechasLlegada = [];
        response.forEach(e => {
          this.fechasEnvio.push({
            id: e.id,
            start: e.fechaOrden,
            title: "Compra pedido " + e.id,
            description: "XD"
          });
          if (e.envio.fechaEstimada) {
            this.fechasLlegada.push(
              {
                id: e.id,
                start: e.envio.fechaEstimada,
                title: "Llegada pedido " + e.id
              });
          }
        });
        const EVENTS: EventInput[] = [
          ...this.fechasEnvio,
          ...this.fechasLlegada
        ];
        this.calendarOptions.events = EVENTS;
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
  }

  public createEventId(): String {
    return String(this.eventGuid++);
  }

  handleEventClick(clickInfo: EventClickArg) {
    let productFound = this.dataCalendar.find((e, index) => {
      if (e.id == clickInfo.event.id) {
        return true;
      }
    });
    let products: String = `
    <div class="row">
    <div class="col-12 col-md-4">
      <img src="./assets/ilustraciones/productos.svg" class="img-fluid" alt="">
    </div>
    <div class="col-12 col-md-8">
    <h5>Estado: ${productFound.estado}</h5>
    <div class='table-responsive'>
    <table class='table'>
      <thead>
        <tr>
          <th><i class="feather icon-bookmark"></i> ID</th>
          <th><i class="feather icon-at-sign"></i> Nombre</th>
          <th><i class="mdi mdi-cash-multiple"></i> Precio</th>
          <th><i class="feather icon-droplet"></i> Color</th>
          <th><i class="feather icon-tag"></i> Talla</th>
          <th><i class="feather icon-gift"></i> Tipo</th>
          <th><i class="feather icon-package"></i> Cantidad</th>
          <th><i class="feather icon-image"></i> Imágen</th>
        </tr>
      </thead>
      <tbody>`;
    productFound.productos.forEach(e => {
      products += `<tr>
      <th>${e.id}</th>
      <td>${e.nombre}</td>
      <td>${new Intl.NumberFormat("de-DE").format(e.precio)}</td>
      <td>
        <div class="btn-group" role="group" aria-label="Basic example">
            <button type="button" class="btn" style="background-color: ${e.color};"></button>
        </div>
      </td>
      <td>${e.talla}</td>
      <td>${e.tipo}</td>
      <td>${e.cantidad}</td>
      <td><img src="${e.imgUrl}" width="35px" srcset=""></td>
    </tr>`
    });
    products += `</tbody>
    </table>
    </div>
    </div>
    </div>
    `;
    Swal.fire({
      title: "",
      html: products,
      width: '80%'
    });
  }

  public nextMonth(): void {
    LoaderComponent.status = true;
    let calendarApi = this.calendarComponent.getApi();
    calendarApi.next();
    const currentDate = moment(calendarApi.getDate()).format("YYYY-MM-DD");
    this.checkCalendarItems(currentDate);
  }

  public prevMonth(): void {
    LoaderComponent.status = true;
    let calendarApi = this.calendarComponent.getApi();
    calendarApi.prev();
    const currentDate = moment(calendarApi.getDate()).format("YYYY-MM-DD");
    this.checkCalendarItems(currentDate);
  }

  public currentMonth(): void {
    LoaderComponent.status = true;
    let calendarApi = this.calendarComponent.getApi();
    calendarApi.today();
    const currentDate = moment(calendarApi.getDate()).format("YYYY-MM-DD");
    this.checkCalendarItems(currentDate);

  }

  public getApi(): any {

  }

}
