import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgbModal} from '@ng-bootstrap/ng-bootstrap';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import { ComprasService } from '../../../../services/compras.service';
import swal from 'sweetalert2';
import { FormControl, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-compras',
  templateUrl: './compras.component.html',
  styleUrls: ['./compras.component.scss']
})
export class ComprasComponent implements OnInit {

  public compras: any;
  public paginador: any;
  public cantidad: number;
  public productos: any;
  public formFilter: FormGroup;
  private mensajesComponent: MensajesComponent = new MensajesComponent(this.router);
  public envio: any;
  public comprasInformation: any;
  public facturaUrl: string;
  public isSearchFilter: boolean;

  constructor(private comprasService: ComprasService, private modalService: NgbModal, private router: Router) { }

  ngOnInit(): void {
    this.formFilter = new FormGroup({
      "idOrder": new FormControl(""),
      "date": new FormControl("")
    });
    this.isSearchFilter = false;
    this.cantidad = 10;
    this.consultarCompras(1, this.cantidad);
    LoaderComponent.status = true;
    this.comprasService.getInformationCompras().subscribe(
      response => {
        this.comprasInformation = response;
        this.comprasInformation.totalGastos = this.comprasInformation.totalGastos == null ? 0 : this.comprasInformation.totalGastos;
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
  }

  public refreshSearch(): void {
    this.formFilter.reset();
    this.formFilter.enable();
    this.isSearchFilter = false;
    this.consultarCompras(1, this.cantidad);
  }

  public changePagination(page: number): void {
    if (this.isSearchFilter) {
      this.findByFilters(page);
    } else {
      this.consultarCompras(page, this.cantidad);
    }
  }

  public respuestasIsView(respuestas: any): boolean {
    for (let respuesta of respuestas) {
      if (respuesta.visto == false) {
        return true;
      }
    }
    return false;
  }

  public openEnvio(content: any, envio: any): void {
    this.envio = envio;
    this.modalService.open(content, { size: "lg" });
  }

  public changeFecha(): void {
    if (!this.formFilter.get("date").value) {
      this.formFilter.get("idOrder").enable();
    } else {
      this.formFilter.get("idOrder").reset();
      this.formFilter.get("idOrder").disable();
    }
  }

  public changeIdOrden(): void {
    if (!this.formFilter.get("idOrder").value) {
      this.formFilter.get("date").enable();
    } else {
      this.formFilter.get("date").reset();
      this.formFilter.get("date").disable();
    }
  }

  public findByFilters(page: number): void {
    LoaderComponent.status = true;
    if (this.formFilter.get("idOrder").value || this.formFilter.get("date").value) {
      this.comprasService.findComprasByIdOrDate(page, this.cantidad, this.formFilter.get("idOrder").value, this.formFilter.get("date").value).subscribe(
        response => {
          this.compras = response.content;
          if(this.compras.length > 0) {
            this.isSearchFilter = true;
          }
          this.paginadorInit(response);
          LoaderComponent.status = false;
        },
        error => {
          console.log(error.error.error);
          this.mensajesComponent.mensajesError(error);
          LoaderComponent.status = false;
        }
      );
    } else {
      swal.fire("¡Ingresa un id de orden o fecha!", "", "warning");
      LoaderComponent.status = false;
    }
  }

  public openProductos(content: any, productos: any): void {
    this.productos = productos;
    this.modalService.open(content, { size: "xl" });
  }

  public openFacturaVenta(content: any, facturaUrl: string): void {
    this.facturaUrl = facturaUrl;
    this.modalService.open(content, { size: "xl" });
  }

  public consultarCompras(pagina: number, totalRegistros: number): void {
    LoaderComponent.status = true;
    this.comprasService.findCompras(pagina, totalRegistros).subscribe(
      response => {
        this.compras = response.content;
        if(this.compras.length > 0) {
          this.isSearchFilter = false;
        }
        this.paginadorInit(response);
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
  }

  public paginadorInit(response: any): void {
    let paginaSiguiente: number;
    let paginaAnterior: number;
    let paginaActual: number = response.number + 1;
    let totalPaginas: number = response.totalPages;
    if (totalPaginas == 0) {
      paginaSiguiente = 0;
      paginaAnterior = 0;
    } else {
      if (paginaActual == totalPaginas) {
        paginaSiguiente = 0;
        paginaAnterior = paginaActual - 1;
      } else {
        if (paginaActual == 1) {
          paginaSiguiente = 2;
          paginaAnterior = 0;
        } else {
          paginaSiguiente = paginaActual + 1;
          paginaAnterior = paginaActual - 1;
        }
      }
    }
    this.paginador = {
      paginaActual: paginaActual,
      totalPaginas: totalPaginas,
      totalRegistros: response.totalElements,
      cantidadRegistros: response.size,
      paginaSiguiente: paginaSiguiente,
      paginaAnterior: paginaAnterior
    };
  }

}
