import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import {
  ChartComponent,
  ApexAxisChartSeries,
  ApexGrid,
  ApexChart,
  ApexXAxis,
  ApexYAxis,
  ApexMarkers,
  ApexStroke,
  ApexLegend,
  ApexTooltip,
  ApexDataLabels,
  ApexFill,
  ApexPlotOptions,
  ApexResponsive,
  ApexNonAxisChartSeries,
  ApexTitleSubtitle,
} from "ng-apexcharts";
import { ComprasService } from 'src/app/services/compras.service';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';

export type ChartOptions = {
  series: ApexAxisChartSeries;
  nonAxisSeries: ApexNonAxisChartSeries;
  colors: string[];
  grid: ApexGrid;
  chart: ApexChart;
  xaxis: ApexXAxis;
  yaxis: ApexYAxis;
  markers: ApexMarkers,
  stroke: ApexStroke,
  legend: ApexLegend,
  responsive: ApexResponsive[],
  tooltip: ApexTooltip,
  fill: ApexFill
  dataLabels: ApexDataLabels,
  plotOptions: ApexPlotOptions,
  labels: string[],
  title: ApexTitleSubtitle
};

@Component({
  selector: 'app-estadisticas',
  templateUrl: './estadisticas.component.html',
  styleUrls: ['./estadisticas.component.scss']
})
export class EstadisticasComponent implements OnInit {

  public barChartOptions: Partial<ChartOptions>;
  private mensajesComponent: MensajesComponent = new MensajesComponent(this.router);
  public titulo: string;
  public isDataInChart: boolean = false;

  constructor(private comprasService: ComprasService, private modalService: NgbModal, private router: Router) { }

  ngOnInit(): void {
    this.findComprasTotalGroupByMonthAndYear();
  }

  public findComprasTotalGroupByYear(): void {
    LoaderComponent.status = true;
    this.comprasService.getComprasGroupByYear().subscribe(
      response => {
        this.isDataInChart = response.length > 0;
        this.titulo = "Total de compras por año.";
        this.barChartOptions = {
          series: [
            {
              name: 'Total: $',
              data: []
            }
          ],
          labels: [],
          colors: ["#f77eb9"],
          grid: {
            borderColor: "rgba(77, 138, 240, .1)",
            padding: {
              bottom: 0
            }
          },
          chart: {
            type: 'bar',
            height: '320',
            parentHeightOffset: 0
          }
        };
        let datos: any = [];
        let labels: any = [];
        for (let data of response) {
          datos.push(data[2]);
          labels.push(data[1]);
        }
        this.barChartOptions.series[0].data = datos;
        this.barChartOptions.labels = labels;
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
  }

  public findComprasTotalGroupByMonthAndYear(): void {
    LoaderComponent.status = true;
    this.comprasService.getComprasGroupByMonthAndYear().subscribe(
      response => {
        this.isDataInChart = response.length > 0;
        this.titulo = "Total de compras por mes.";
        this.barChartOptions = {
          series: [
            {
              name: 'Total: $',
              data: []
            }
          ],
          labels: [],
          colors: ["#f77eb9"],
          grid: {
            borderColor: "rgba(77, 138, 240, .1)",
            padding: {
              bottom: 0
            }
          },
          chart: {
            type: 'bar',
            height: '320',
            parentHeightOffset: 0
          }
        };
        let datos: any = [];
        let labels: any = [];
        for (let data of response) {
          datos.push(data[2]);
          labels.push(data[1]);
        }
        this.barChartOptions.series[0].data = datos;
        this.barChartOptions.labels = labels;
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
  }

}
