import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { Utilidades } from '../services/utilidades';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class CalendarioService {

  private utilidades: Utilidades = new Utilidades();

  constructor(private http: HttpClient) { }

  public getVentasNotFinished(date: any): Observable<any> {
    return this.http.get(environment.backEndPoint + "/api/venta/get/notFinish/" + date, { headers: this.utilidades.getHeaders() }).pipe(
      catchError(
        error => {
          return throwError(error);
        }
      )
    );
  }

}
