import { Component, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { QuejasService } from 'src/app/services/quejas.service';
import { MessagesComponent } from 'src/app/views/layout/navbar/messages/messages.component';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import swal from 'sweetalert2';

@Component({
  selector: 'app-complaints',
  templateUrl: './complaints.component.html',
  styleUrls: ['./complaints.component.scss']
})
export class ComplaintsComponent implements OnInit {

  private ventaId: number;
  private pedidoId: number;
  public modalCrearQueja: NgbModalRef;
  public queja: FormControl;
  private mensajesComponent: MensajesComponent = new MensajesComponent(this.router);
  public isSearchQueja: boolean;
  public quejas: any[];
  public mensajeSearch: FormControl;
  public quejaSelect: any;
  public respuestas: any;
  public paginadorRespuestas: any;
  public paginadorQuejas: any;
  public sizeQuejasNotView: number;

  constructor(private activatedRouter: ActivatedRoute, private router: Router, private modalService: NgbModal, private quejasService: QuejasService) { }

  ngOnInit(): void {
    this.queja = new FormControl("", [Validators.required, Validators.maxLength(200)]);
    this.sizeQuejasNotView = 0;
    this.paginadorRespuestas = null;
    this.quejaSelect = null;
    this.quejas = [];
    this.mensajeSearch = new FormControl("", [Validators.required, Validators.maxLength(20)]);
    this.isSearchQueja = false;
    this.activatedRouter.params.subscribe(
      params => {
        if (params["ventaId"] && params["pedidoId"]) {
          this.ventaId = params["ventaId"];
          this.pedidoId = params["pedidoId"];
          this.getRespuestasNotView();
          this.findQuejasByVentaId(1);
        } else {
          this.router.navigate(["/cliente/catalogo"]);
        }
      }
    );
  }

  public paginadorInit(response: any, isQuejas: boolean): void {
    let paginaSiguiente: number;
    let paginaAnterior: number;
    let paginaActual: number = response.number + 1;
    let totalPaginas: number = response.totalPages;
    if (totalPaginas == 0) {
      paginaSiguiente = 0;
      paginaAnterior = 0;
    } else {
      if (paginaActual == totalPaginas) {
        paginaSiguiente = 0;
        paginaAnterior = paginaActual - 1;
      } else {
        if (paginaActual == 1) {
          paginaSiguiente = 2;
          paginaAnterior = 0;
        } else {
          paginaSiguiente = paginaActual + 1;
          paginaAnterior = paginaActual - 1;
        }
      }
    }
    if (isQuejas) {
      this.paginadorQuejas = {
        paginaActual: paginaActual,
        totalPaginas: totalPaginas,
        totalRegistros: response.totalElements,
        cantidadRegistros: response.size,
        paginaSiguiente: paginaSiguiente,
        paginaAnterior: paginaAnterior
      };
    } else {
      this.paginadorRespuestas = {
        paginaActual: paginaActual,
        totalPaginas: totalPaginas,
        totalRegistros: response.totalElements,
        cantidadRegistros: response.size,
        paginaSiguiente: paginaSiguiente,
        paginaAnterior: paginaAnterior
      };
    }
  }

  public getRespuestasNotView(): void {
    LoaderComponent.status = true;
    this.quejasService.getRespuestas(1, 3).subscribe(
      response => {
        MessagesComponent.mensajes = response.content;
        MessagesComponent.sizeMessages = response.totalElements;
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
  }

  public openRespuestas(content: any, queja: any): void {
    this.quejaSelect = queja;
    this.getRespuestasByQuejaAndUser(1, 3);
    this.modalService.open(content, { size: "xl" });
  }

  public getRespuestasByQuejaAndUser(page: number, size: number): void {
    LoaderComponent.status = true;
    this.quejasService.getRespuestasByQuejaIdAndUserId(page, size, this.quejaSelect.id).subscribe(
      response => {
        this.respuestas = response.content;
        this.paginadorInit(response, false);
        this.respuestaView();
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
      }
    );
  }

  public respuestaView(): void {
    LoaderComponent.status = true;
    let respuestasIds: number[] = [];
    for (let respuesta of this.respuestas) {
      if (!respuesta.visto) {
        respuestasIds.push(respuesta.id);
      }
    }
    if (respuestasIds.length > 0) {
      this.quejasService.respuestasViews(respuestasIds).subscribe(
        response => {
          if (this.ventaId) {
            this.findQuejasByVentaId(1);
          }
          this.getRespuestasNotView();
          LoaderComponent.status = false;
        },
        error => {
          this.mensajesComponent.mensajesError(error);
        }
      );
    }
  }

  public findQuejaByMensaje(): void {
    if (this.mensajeSearch.valid) {
      LoaderComponent.status = true;
      this.quejasService.getQuejasByMensajeAndVentaId(this.ventaId, this.mensajeSearch.value, 1, 10).subscribe(
        response => {
          this.isSearchQueja = true;
          this.quejas = response.data.content;
          this.sizeQuejasNotView = response.sizeResponseNotView;
          this.paginadorInit(response.data, true);
          LoaderComponent.status = false;
        },
        error => {
          this.mensajesComponent.mensajesError(error);
          LoaderComponent.status = false;
        }
      );
    } else {
      swal.fire("¡Ingresa un mensaje!", "", "warning");
    }
  }

  public paginadorConfig(page: number): void {
    if (this.isSearchQueja) {
      this.quejasService.getQuejasByMensajeAndVentaId(this.ventaId, this.mensajeSearch.value, page, 10).subscribe(
        response => {
          this.quejas = response.data.content;
          this.sizeQuejasNotView = response.sizeResponseNotView;
          this.paginadorInit(response.data, true);
          LoaderComponent.status = false;
        },
        error => {
          this.mensajesComponent.mensajesError(error);
          LoaderComponent.status = false;
        }
      );
    } else {
      this.findQuejasByVentaId(page);
    }
  }

  public createQueja(): void {
    LoaderComponent.status = true;
    if (this.queja.valid) {
      this.quejasService.createQueja(this.queja.value, this.pedidoId).subscribe(
        response => {
          this.modalCrearQueja.close();
          this.findQuejasByVentaId(1);
          this.queja.reset();
          swal.fire("¡Queja creada!", "Espera nuestra pronta respuesta.", "success");
          LoaderComponent.status = false;
        },
        error => {
          this.mensajesComponent.mensajesError(error);
          LoaderComponent.status = false;
        }
      );
    } else {
      swal.fire("¡Ingresa una queja!", "", "error");
      LoaderComponent.status = false;
    }
  }

  public openCrearQueja(content: any): void {
    this.modalCrearQueja = this.modalService.open(content, { size: "xl" });
  }

  public findQuejasByVentaId(page: number): void {
    LoaderComponent.status = true;
    this.quejasService.getQuejasByventaId(this.ventaId, page, 10).subscribe(
      response => {
        if (this.isSearchQueja) {
          this.isSearchQueja = false;
          this.mensajeSearch.reset();
        }
        this.quejas = response.data.content;
        this.sizeQuejasNotView = response.sizeResponseNotView;
        this.paginadorInit(response.data, true);
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
  }

}
