import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LoaderComponent } from 'src/app/views/loader/loader.component';
import { MensajesComponent } from 'src/app/views/mensajes/mensajes.component';
import { CotizacionService } from '../../../../services/cotizacion.service';
import swal from 'sweetalert2';
import { EventHandlerVars } from '@angular/compiler/src/compiler_util/expression_converter';

@Component({
  selector: 'app-cotizacion',
  templateUrl: './cotizacion.component.html',
  styleUrls: ['./cotizacion.component.scss']
})
export class CotizacionComponent implements OnInit {

  public cotizacion: any;
  private mensajesComponent: MensajesComponent = new MensajesComponent(this.router);

  constructor(private cotizacionService: CotizacionService, private router: Router) { }

  ngOnInit(): void {
    this.cotizacion = null;
    this.getCotizacion();
  }

  public cotizacionFinishEdit(): void {
    swal.fire({
      title: '¿Ya finalizaste de editar tu cotización?',
      text: "",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Si!',
      cancelButtonText: 'No!'
    }).then((result) => {
      if (result.isConfirmed) {
        if (this.cotizacion.productos.length > 0) {
          LoaderComponent.status = true;
          this.cotizacionService.cotizacionFinishEdit().subscribe(
            response => {
              swal.fire("¡Cotización enviada!", "Vamos a revisar tu cotización, espera nuestra pronta respuesta, no dudes en contactanos.", "success");
              this.ngOnInit();
              LoaderComponent.status = false;
            },
            error => {
              this.mensajesComponent.mensajesError(error);
              LoaderComponent.status = false;
            }
          );
        } else {
          swal.fire("¡Selecciona un producto por favor!", "", "warning");
        }
      }
    });
  }

  public sendReportCotizacion(): void {
    if (this.cotizacion.productos.length > 0) {
      LoaderComponent.status = true;
      this.cotizacionService.sendReportCotizacion().subscribe(
        response => {
          swal.fire("¡Cotización enviada!", "Por favor revisa tu correo, hemos enviado un correo con tu cotización.", "success");
          LoaderComponent.status = false;
        },
        error => {
          this.mensajesComponent.mensajesError(error);
          LoaderComponent.status = false;
        }
      );
    } else {
      swal.fire("¡Selecciona un producto por favor!", "", "warning");
    }
  }

  public deleteCotizacion(): void {
    swal.fire({
      title: '¿Estas seguro de eliminar la cotización?',
      text: "",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Si!',
      cancelButtonText: 'No!'
    }).then((result) => {
      if (result.isConfirmed) {
        LoaderComponent.status = true;
        this.cotizacionService.deleteCotizacion().subscribe(
          response => {
            this.ngOnInit();
            swal.fire("¡Cotización eliminada!", "", "success");
            LoaderComponent.status = false;
          },
          error => {
            this.mensajesComponent.mensajesError(error);
            LoaderComponent.status = false;
          }
        );
      }
    });
  }

  public deleteProductoCotizacion(productoCotizacionId: number): void {
    swal.fire({
      title: '¿Estas seguro de eliminar el producto?',
      text: "",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Si!',
      cancelButtonText: 'No!'
    }).then((result) => {
      if (result.isConfirmed) {
        LoaderComponent.status = true;
        this.cotizacionService.deleteProductoCotizacion(productoCotizacionId).subscribe(
          response => {
            this.ngOnInit();
            LoaderComponent.status = false;
          },
          error => {
            this.mensajesComponent.mensajesError(error);
            LoaderComponent.status = false;
          }
        );
      }
    });
  }

  public changeCantidad(event: any, productoCotizacionId: number): void {
    let cantidad: number = event.target.value;
    if (cantidad > 0 && cantidad <= 99999) {
      LoaderComponent.status = true;
      this.cotizacionService.updateCantidad(productoCotizacionId, cantidad).subscribe(
        response => {
          swal.fire("¡Cantidad actualizada!", "", "success");
          this.ngOnInit();
          LoaderComponent.status = false;
        },
        error => {
          this.mensajesComponent.mensajesError(error);
          LoaderComponent.status = false;
        }
      );
    } else {
      swal.fire("¡Ingresa una cantidad correcta!", "", "info");
    }
  }

  public createCotizacion(): void {
    LoaderComponent.status = true;
    this.cotizacionService.createCotizacion().subscribe(
      response => {
        swal.fire("¡Cotización creada con el No:" + response.id + "!", "", "success");
        this.ngOnInit();
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
  }

  public getCotizacion(): void {
    LoaderComponent.status = true;
    this.cotizacionService.getCotizacion().subscribe(
      response => {
        this.cotizacion = response;
        LoaderComponent.status = false;
      },
      error => {
        this.mensajesComponent.mensajesError(error);
        LoaderComponent.status = false;
      }
    );
  }

}
